    overflow: hidden; } }

.view {
  display: flex;
  flex-direction: column;
  height: 100%;
  max-height: 100%; }
  .view > .view-content {
    flex-grow: 1;
    flex-shrink: 1;
    overflow-y: auto;
    height: 100%;
    width: 100%;
    margin: 0 auto; }

a[href] {
  color: #3F51B5; }
