    ) {
        foo: bar;
    }    

    output:
    .apply-is-foo.apply-is-danger .component, 
    .component.is-foo.is-danger, 
    .apply-is-bar.apply-is-danger .component, 
    .component.is-bar.is-danger {
        foo: bar; 
    }
*/
